
.review {
    margin: 30px auto;
    width: 500px;
}
.review__date {
    display: inline-block;
    font-size: 18px;
    margin-right: 50px;
    font-weight: 600;
}
.review__text {
    text-align: left;
    margin-top: 25px;
    margin-bottom: 50px;
    padding-left: 250px;
    font-size: 16px;
    font-style: italic;
}
review__rate {
    display: inline-block;
}
.review i {
    font-size: 24px !important;
}



