
.col-balancer {
    height: 450px;
}
.profile-container {
    text-align: left;
}
.profile__daytime {
    max-width: 300px;
    width: 100%;
}
.is-error input {
    border-color: #ff4949;
}
.profile__error {
    color: #ff4949;
    font-size: 12px;
    line-height: 1;
    padding-top: 6px;
}
.profile__field {
    position: relative;
}
.profile-container .el-input
{
    max-width: 300px !important;
    width: 100% !important;
}
.profile__input  {
    font-size: 20px !important;
}
.profile__title {
    text-align: center;
    margin-left: 0;
    padding: 0;
    font-size: 26px;
    margin-top: 40px;
    margin-bottom: 10px;
}
@media (min-width: 1024px) {
.profile__title img {
        margin-left: -150px;
}
}
.profile__title img {
    width: 40px;
    display: inline-block;
    padding-bottom: 15px;
}
.profile__title div {
    display: inline-block;
}
.profile__btns {
    margin-top: 15px;
    margin-bottom: 50px;
    max-width: 300px;
    padding: 0;
}
.profile__btns .md-button:first-child {
    margin-left: 0;
}
.profile__btns .md-button:last-child {
}




