
.md-dialog-container {
    z-index: 9999999 !important;
}
.modal-title {
    padding: 5px 20px 15px;
    border-bottom: solid darkgray 1px;
}
.rate {
    text-align: center;
    margin-bottom: 50px;
}
.rate i {
    font-size: 38px;
}
.rate .el-rate__text {
    font-size: 28px;
    display: inline-block;
    text-align: center;
    min-width: 200px;
}
.feedback {
    text-align: center;
}
.feedback textarea {
    font-size: 18px;
}
.feedback-label {
    text-align: left;
    font-size: 18px;
    margin-bottom: 20px;
}
.content-container {
    width: 500px;
    padding: 5px 20px;
}
.submit-btn {
    bottom: 10px;
    right: 10px;
}
@media (max-width: 1023px) {
.content-container {
        width: auto;
}
.rate i {
        font-size: 28px;
}
}



