
.dtm-container > * {
    display: inline-block;
    width: 100%;
    margin: 0 !important;
    padding: 0 !important;
    box-sizing: border-box;
}
.time-container {
    margin-top: 15px;
}
.dtm-container__day {
    width: 30%;
}
.dtm-container__month {
    width: 30%;
}
.dtm-container__year {
    width: 37%;
}





