
* {
    box-sizing: border-box;
}
.no-select {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.up-button {
    position: fixed;
    height: 50px;
    width: 56px;
    cursor: pointer;
    right: 0;
    top: 0;
    z-index: 99;
    background-color: #cccccc;
    opacity: 0.5;
}
.up-button i {
    font-size: 46px;
    width: 100%;
    height: 100%;
}
.up-button:hover {
    opacity: 1;
}
.social-sharing {
    position: absolute !important;
    bottom: 5px;
    right: 25px;
}
.social-sharing .fa {
    padding-left: 10px;
    cursor: pointer;
}
.social-sharing .fa:hover {
    color: #0074D9;
}
.dynamic-info div {
    display: inline-block;
}
.dynamic-info div:first-child {
    margin-right: 20px;
}
.plus .category-icon i {
    color: #AED581;
}
.minus .category-icon i {
    color: #FF8A65;
}
.dynamic-icons-block img {
    width: 45px;
}
.dynamic-icons-block {
    position: relative;
    margin-top: 9px;
    margin-bottom: 10px;
}
.dynamic-icons-block > * {
    display: inline-block;
}
.dynamic-icons-block div:nth-child(2) {
    margin-right: 50px;
}
.dynamic-icons-block div:nth-child(2n+1) {
    margin-right: 10px;
}
.day-info {
    height: auto;
    background-color: white;
    box-shadow: 0 2px 8px gray;
    margin-top: 25px;
    margin-bottom: 25px;
}
.day-title {
    font-size: 24px;
    font-weight: 500;
    padding-top: 5px;
}
.day-category {
    height: auto;
    background-color: white;
    box-shadow: 0 2px 8px gray;
    position: relative;
    margin-bottom: 25px;
    text-align: left;
}
.img-container {
    width: 60px;
    height: 50px;
    position: absolute;
    top: -15px;
    left: -10px;
    z-index: 2;
}
.color-block {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 35px;
    background-color: #E8E8E8;
    z-index: 1;
}
.category-name {
    font-size: 20px;
    font-weight: 500;
    width: 100%;
    height: 100%;
    text-align: left;
    padding-left: 75px;
    padding-top: 7px;
}
.category-content {
    margin-top: 50px;
    word-break: normal;
    text-align: left;
    margin-bottom: 20px;
    margin-left: 30px;
    font-size: 16px;
    color: black;
    padding-left: 30px;
}
.category-content.main {
    margin-left: 30px;
    margin-bottom: 50px;
}
.category-content > * {
    position: relative;
    margin-bottom: 10px;
}
.category-icon {
    position: absolute;
    left: -30px;
    top: 0px;
}
.material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 22px; /* Preferred icon size */
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;

    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;

    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;

    /* Support for IE. */
    font-feature-settings: 'liga';
}
.promo__btn {
    margin-left: -7px;
}
.promo__link {
    margin-top: 10px;
    color: indigo;
    cursor: pointer;
    text-decoration: underline;
}




