
#app {
    font-family: 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    overflow: hidden;
}
#app a:visited {
    text-decoration: none;
}
#app a:focus {
    text-decoration: none;
}
.closeBtn {
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 99;
    cursor: pointer;
}
.closeBtn i {
    font-size: 26px;
}
.closeBtn:hover {
    color: #0074D9 !important;
}
body {
    background-color: #fafafa !important;
}
#lang-switcher {
    position: absolute;
    right: 15px;
    text-align: left;
    z-index: 2;
}
#lang-switcher button {
    padding: 0 !important;
    width: 35px;
    min-width: 0;
}
.help-link {
    cursor: pointer;
}
.contacts-link {
    color: gainsboro !important;
    background-color: transparent;
    cursor: pointer;
    margin-left: 15px;
}
.contacts-link:hover {
    text-decoration: none !important;
}
.vline {
    border-left: 1px solid white;
    width: 1px;
    padding: 0;
    margin-left: 5px;
    margin-right: 10px;
    height: 40px;
}
.login-btn {
    position: absolute;
    right: 90px;
    top: 15px;
    display: block;
    width: 36px;
    cursor: pointer;
}




/*  */
@media (min-width: 1024px) {
.main-sidenav.md-sidenav .md-sidenav-content {
        pointer-events: auto;
        transform: translate3d(0, 0, 0) !important;
        box-shadow: 0 8px 10px -5px rgba(0, 0, 0, .2), 0 16px 24px 2px rgba(0, 0, 0, .14), 0 6px 30px 5px rgba(0, 0, 0, .12);
}
#app {
        margin-left: 300px !important;
}
#lang-switcher button {
        width: 50px;
}
.today-btn {
        margin-left: 80px;
}
.login-btn {
        right: 135px;
}
.contacts-link {
        margin-left: 25px;
}
}
.logo {
    width: 150px;
    display: block;
    margin: 5px auto 10px;
}
.categories {
    margin-top: 20px;
}
.category {
    width: 90%;
    margin: 4px !important;
    padding: 0;
    text-align: left !important;
}
.categories :not(.active) {
    color: black !important;
}
.categories .category.active {
    background-color: whitesmoke;
}
.main-title {
    margin-left: 30px !important;
}







/* Tooltips styles */
.next-btn {
    color: white;
    border-color: hotpink;
    background-color: hotpink;
    font-size: 18px;
    font-weight: 600;
    width: 150px;
}
.next-btn:hover {
    background-color: deeppink;
}
.next-btn:active {
    border-color: hotpink
}
.skip-btn {
    color: white;
    border-color: darkgray;
    width: 150px;
}
.skip-btn:hover {
    background-color: darkgray;
}
.skip-btn:active {
    border-color: darkgray;
}
.enjoyhint_close_btn {
    border-color: darkgray;
    color: white;
}
.enjoyhint_close_btn:hover {
    background-color: darkgray;
}
.enjoyhint_close_btn:active {
    border-color: darkgray;
}
.empty-btn {
    display: none;
}







/* end tooltips styles */
.header {
    position: relative;
}
.login-modal__content {
    width: 400px;
}
.bookmarks-modal {
    font-size: 18px;
    text-align: center;
}
.bookmarks-modal strong {
    font-weight: 900;
    font-size: 20px;
}
.bookmarks-modal__thanks {
    margin-top: 10px;
}
.bookmarks-modal__thanks i {
    color: rgb(255, 142, 50)
}



