
@keyframes gift {
0% {
    transform: translateY(0%);
}
50% {
    transform: translateY(-25%);
}
100% {
    transform: translateY(0);
}
}
@media (max-width: 1024px) {
#promo-btn[data-v-6a4f19ce] {
    position: fixed;
    cursor: pointer;
    z-index: 1000;
    right: 30px;
    bottom: 20px;
    width: 50px;
    height: 50px;
    animation: gift 1s infinite ease-in-out;
    border-radius: 50%;
}
#promo-btn img[data-v-6a4f19ce] {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      overflow: hidden;
}
#promo-btn .desc[data-v-6a4f19ce] {
      position: absolute;
      transition: all 0.5s ease-in-out;
      top: 30%;
      font-size: 0px;
      font-weight: bold;
      font-style: italic;
      width: 50px;
      overflow: hidden;
}
#promo-btn[data-v-6a4f19ce]:hover {
      animation-play-state: paused;
}
#promo-btn:hover .desc[data-v-6a4f19ce] {
      transform: translateX(-100%);
}
}
@media (min-width: 1025px) {
#promo-btn[data-v-6a4f19ce] {
    position: fixed;
    cursor: pointer;
    z-index: 1000;
    right: 30px;
    bottom: 20px;
    width: 100px;
    height: 100px;
    animation: gift 1s infinite ease-in-out;
    border-radius: 50%;
}
#promo-btn img[data-v-6a4f19ce] {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      overflow: hidden;
}
#promo-btn .desc[data-v-6a4f19ce] {
      position: absolute;
      transition: all 0.5s ease-in-out;
      top: 30%;
      font-size: 18px;
      font-weight: bold;
      font-style: italic;
      width: 100px;
      overflow: hidden;
}
#promo-btn[data-v-6a4f19ce]:hover {
      animation-play-state: paused;
}
#promo-btn:hover .desc[data-v-6a4f19ce] {
      transform: translateX(-100%);
}
}



